"use client";

import { useState, useRef, useEffect } from 'react';
import { FFmpeg } from '@ffmpeg/ffmpeg';
import { fetchFile, toBlobURL } from '@ffmpeg/util';

export const useFFmpeg = () => {
    const [loaded, setLoaded] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const ffmpegRef = useRef(new FFmpeg());
    const messageRef = useRef<HTMLParagraphElement | null>(null);

    const load = async () => {
        setIsLoading(true);
        const baseURL = 'https://unpkg.com/@ffmpeg/core@0.12.6/dist/umd';
        const ffmpeg = ffmpegRef.current;

        // Log messages
        ffmpeg.on('log', ({ message }) => {
            console.log(message);
        });

        try {
            await ffmpeg.load({
                coreURL: await toBlobURL(`${baseURL}/ffmpeg-core.js`, 'text/javascript'),
                wasmURL: await toBlobURL(`${baseURL}/ffmpeg-core.wasm`, 'application/wasm'),
                workerURL: await toBlobURL(`${baseURL}/ffmpeg-core.worker.js`, 'text/javascript'),
            });
            setLoaded(true);
        } catch (e) {
            console.error("FFmpeg load failed", e);
        } finally {
            setIsLoading(false);
        }
    };

    const extractSubtitles = async (file: File): Promise<string | null> => {
        if (!loaded) return null;
        const ffmpeg = ffmpegRef.current;

        // Mount file
        const data = await fetchFile(file);
        await ffmpeg.writeFile('input.mkv', data);

        // Check for subtitles
        // We'll try to extract the first subtitle track (0:s:0) to a VTT file
        // Command: ffmpeg -i input.mkv -map 0:s:0 -f webvtt output.vtt

        // First we might want to probe, but for simplicity let's try blind extraction of first track
        try {
            await ffmpeg.exec(['-i', 'input.mkv', '-map', '0:s:0', '-f', 'webvtt', 'output.vtt']);
            const data = await ffmpeg.readFile('output.vtt');

            // Cleanup
            await ffmpeg.deleteFile('input.mkv');
            await ffmpeg.deleteFile('output.vtt');

            // Convert to blob
            const blob = new Blob([data as any], { type: 'text/vtt' });
            return URL.createObjectURL(blob);
        } catch (e) {
            console.error("Extraction failed or no subtitles", e);
            try { await ffmpeg.deleteFile('input.mkv'); } catch { }
            return null;
        }
    };

    return { loaded, isLoading, load, extractSubtitles };
}
